/////// FONT FAMILY ////////
$head-font: 'Racho', cursive;
$para-font: 'Raleway' sans-serif;

////// FONT SIZE ////////
$header-size: 3.5em;
$alt-header: 2em;
$para-size: 1.2em;

///// SPACING //////
$line-space: 1.3em;
$letter-space: .12em;
$word-spacing: .1em;

/////// COLORS ///////
$prime-color: #321A37;
$secondary-color: #9966cc;
$white: #fff;

//////// lINEAR-GRADIENT /////////
$cover: linear-gradient($secondary-color, $white);
$rev-cover: linear-gradient($white, $secondary-color);
$switch: linear-gradient(to right bottom, $secondary-color, $prime-color);
$gradient-primary: linear-gradient(180deg, rgba(59, 35, 93, 0.6) 0%, rgba(255, 255, 255, 0.6) 100%);
$gradient-secondary: linear-gradient(122.32deg, rgba(229, 229, 229, 0.75) 0%, rgba(59, 35, 93, 0.75) 100%);

////// Button CSS //////
$btn-width: 110px;
$btn-height: 35px;
$btn-radius: 5px;
$btn-border: none;
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


h1, h2, h3 {
  font-family: $head-font;
}

.btn {
  color: $prime-color;
}

// Navigation
.navbar {
  height: 65px;
}

.navbar-brand img {
  width: 100px;
  height: 100px;
  background-color: #fff;
  border-radius: 50%;
  margin-left: 30px;
  position: absolute;
  top: 12px;
  border: medium solid $prime-color;
}

.navigation {
  background: $prime-color;
}

// Homepage

// Jumbotron
.jumbotron {
  background: $gradient-primary;
  border-radius: 0 !important;
}

.display-3 {
  font-family: $head-font;
}

// About Block

#home-about {
  height: 50vh;
  margin-top: 50px;

  .container {
    align-self: center;
  
  }
}

// Event Block

#home-updates {
  height: 60vh;
  background: #E5E5E5;
}

// Contact Block

#home-contact {
  background: $gradient-secondary;
  
  height: 50vh;

  h2 {
    font-family: $head-font;
    font-size: $alt-header;
  }

  button {
    box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    font-family: $para-font;
    background: #E5E5E5;
  }
}

// Organizer Block

#organizers {
  margin: 100px 0px;
}

.card {
  border: none !important;
}

// Contact Page



// Footer
.page-footer {
  background-color: $prime-color;
  color: white;

  a {
    color: white;
  }
}

.footer-copyright {
  background-color: $secondary-color;
}

//////// ABOUT PAGE ////////
.aboutUs {
  width: 100%;
  text-align: center;
  height: 100vh;
  overflow: hidden;

  &.about-img {
    background-image: url(./assets/images/BlackCodes.png);
  }
}

.mission-state {
  background: linear-gradient(180deg, rgba(59, 35, 93, 0.6) 0%, rgba(255, 255, 255, 0.6) 100%);
  max-width: 100vw;
  height: 25vh;
  text-align: center;

  h2 {
    display: inline-block;
  }

  p {
    font-family: $para-font;
    font-size: $para-size;
  }
}


/* At the top level of your code */
@media screen and (max-width: 480px) {
  .navbar-brand img {
    width: 70px;
    height: 70px;
    background-color: #fff;
    border-radius: 50%;
    margin-left: 5px;
    position: absolute;
    top: 25px;
    border: medium solid $prime-color;
  }

  .navbar-nav {
    background-color:rgba($color: $prime-color, $alpha: 0.5);
  }

  .display-3 {
    font-size: 2.5em !important;
  }
}